<script>
import $ from 'jquery';
import {Loadmore} from 'mint-ui'
import 'mint-ui/lib/loadmore/style.css'
export default {
	components: {
      	'mt-loadmore': Loadmore
    },
    data() {
		return {
			allLoaded: false,
				topPullText: '123213',
			loadmore: {

			},
			teacherList: [{},{},{},{},{},{},{},{},{},{}]
		}
    },
    mounted() {
    	console.log($('.home-head').height(),$(window).height())
    	setTimeout(() => {
    	$('.main').height($(window).height()-$('.home-head').height());

    	},0)
    },
    methods: {
    	//标题切换
    	titleClick() {

    	},
    	// 加载更多
    	loadTop(id) {
    		const self = this;
    		setTimeout(() => {
    			this.$refs.loadmore.onTopLoaded(id);
    			this.$refs.loadmore.topPullText = '下拉刷新↓'
    		}, 2000)
    	},
    	loadBottom(id) {
			let self = this;
			this.$refs.loadmore.bottomPullText = '上拉刷新↑'
			setTimeout(function() {
				self.teacherList.push({})
				if(self.$refs.loadmore) {
					self.$refs.loadmore.onBottomLoaded(id);
					

				}
			}, 2000)
    	}


    }
}
