<template>
	<div class="aaa">
        <div @click="aaa">返回</div>
        <a href="">支付</a>   
        {{id}}
        <div>页面2</div>
        <div class="bottom" @click="next">预约</div>

    </div>
</template>
<script>
/**
 *css
 *
 *外部css
 *
*/


/**
 *
 *外部js
 *
*/

export default {
	components: {
     	
    },
    data() {
		return {
			id: this.$route.query.id
		}
    },
    mounted() {
   		console.log(this)
    },
    methods: {
    	aaa() {
            // this.$router.push('/home')
            console.log(this);
            this.$router.goBack();
        },
        next() {
            this.$router.push({path: '/order', query: {id: this.$route.query.id}})
        }
    }
}
</script>

<style scoped>
	.aaa{
        width: 100%;
        height: 100%;
        background: green;
    }
    .bottom{
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
    }
</style>


