<template>
  <div class="mask" @click="toggle">
    <slot></slot>
  </div>
</template>

<script>

export default {
  data () {
    return {
      
    }
  },
  methods: {
    toggle() {
      this.$emit('emitEvent')
    }
  }
}
</script>


<style scoped>
  .mask{
    width: 100%;
    height: 100%;
    background: #000;
    background: rgba(0,0,0,.3);
    position: fixed;
    left: 0;
    top: 0;
  }
</style>



